import  React, {useEffect, useState} from 'react';
import axios from "axios";
import CardMedia from '@mui/material/CardMedia';

const drawerWidth = 220;
const TrainingMaterial = ({history, courseId}) => {
    const[trainingMaterial, setMaterial] = useState([]);


    useEffect(() => {
        if(!localStorage.getItem("authToken")){
            history.push("/login");
        } 
        const fetchAllCodeLabs = async() => {
          const config = {
            headers: {
              "Content-Type": "application/json",
              Authorization: `Bearer ${localStorage.getItem("authToken")}`,
            },
          };
        try {
              const { data } = await axios.get(`/api/upload/getCourseAllFiles/${courseId}`, config);
              console.log("trainingMaterial",data);   
              setMaterial(data.res);  
            
            } 
            catch (error) {
              console.log(error.response.data.error);
            }
          };
          
          fetchAllCodeLabs();
        },[history]);
    return (
        <>
    <div style={{"display":"flex", "justifyContent":"space-evenly", "flexWrap":"wrap"}}>
      
      {trainingMaterial.map(({fileName}) => (
      <div style={{"width":"30%", "display":"flex", "border":"solid .5px black", "flexDirection":"column", "borderRadius":"5px", "margin":".5rem"}}>
                <CardMedia
                    sx={{
                      backgroundImage: 'url(data:image/jpeg;base64,)',
                      backgroundRepeat: 'no-repeat',
                      backgroundColor: (t) =>
                        t.palette.mode === 'light' ? t.palette.grey[50] : t.palette.grey[900],
                      backgroundSize: 'cover',
                      backgroundPosition: 'center',
                      width:"100%"
                    }}
            component="img"
            height="160"
          />
          <a  target = "_blank" href ={`http://localhost:8080/public/images/${fileName}`}>{fileName}</a>
      </div> 
      ))}
         
      </div>
</>

     );
}
 
export default TrainingMaterial;