import  React, {useEffect, useState} from 'react';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import ResponsiveDrawer from './../../components/Navbar/navbar';
import CardMedia from '@mui/material/CardMedia';
import axios from "axios";
import UploadFile from '../UploadFile/uploadFile';

const drawerWidth = 220;
const StudyMaterial = ({history}) => {
    const[files, setfiles] = useState([]);


    useEffect(() => {
        if(!localStorage.getItem("authToken")){
            history.push("/login");
        } 
        const fetchAllCourses = async() => {
          const config = {
            headers: {
              "Content-Type": "application/json",
              Authorization: `Bearer ${localStorage.getItem("authToken")}`,
            },
          };
        try {
              const { data } = await axios.get(`api/upload/getAllFile`, config);
              console.log("pdfs",data);   
              setfiles(data.res);
            
            } 
            catch (error) {
              console.log(error.response.data.error);
            }
          };
          
          fetchAllCourses();
        },[history]);
    return (
        
        <Box sx={{ display: 'flex' }} >
        <ResponsiveDrawer></ResponsiveDrawer> 
        <Box
        component="main"
        sx = {{ flexGrow: 1, p: 3, width: { sm: `calc(100% - ${drawerWidth}px)` } }}
      >
        <Toolbar />
    <Box component="div"
    sx={{display:"flex"}}>
      {(true)?(<>
<UploadFile></UploadFile>
              </>

      ):null}


    </Box>
    <div style={{"display":"flex", "justifyContent":"space-evenly", "flexWrap":"wrap"}}>
      
    {files.map(({fileName}) => (
    <div style={{"width":"30%", "display":"flex", "border":"solid .5px black", "flexDirection":"column", "borderRadius":"5px", "margin":".5rem"}}>
              <CardMedia
                  sx={{
                    backgroundImage: 'url(data:image/jpeg;base64,)',
                    backgroundRepeat: 'no-repeat',
                    backgroundColor: (t) =>
                      t.palette.mode === 'light' ? t.palette.grey[50] : t.palette.grey[900],
                    backgroundSize: 'cover',
                    backgroundPosition: 'center',
                    width:"100%"
                  }}
          component="img"
          height="160"
        />
      <a  target = "_blank" href ={`http://localhost:8080/public/images/${fileName}`}>{fileName}</a>
    </div> 
    ))}
       
    </div>
      </Box>
      </Box>
     );
}
 
export default StudyMaterial;